* {
  margin: 0;
  padding: 0;
  background-color: rgb(67, 67, 73);
}
body {
  display: flex;
  justify-content: center;
  justify-items: center;

  width: 50vw;
  height: 50vh;
  margin: 15%;
}
form {
  display: flex;
  justify-content: center;
  justify-items: center;
  height: 2rem;
  box-shadow: 10px 5px 5px rgb(158, 27, 27);
  button,
  input {
    padding-left: 10%;
    color: whitesmoke;
    width: 15rem;
    border: transparent;
    background-color: rgba(165, 42, 42, 0.336);
  }
  margin: 1rem;
}
.content div {
  display: flex;
  justify-content: center;
  justify-items: center;
  line-height: 1.6;
  border: rgba(165, 42, 42, 0.459) 2px solid;
  color: rgb(211, 203, 203);
}
.content {
  box-shadow: 10px 5px 5px rgb(158, 27, 27);
  border: 1px brown solid;
}
.bordor button {
  background-color: yellow;
  height: 2rem;
  width: 3rem;
}
.lang {
  background-color: yellow;
  height: 2rem;
  width: 15rem;
}
.image {
  margin-left: 18%;
  width: 20rem;
  height: 20rem;
  border-radius: 50%;
  justify-content: center;
  justify-items: center;
  animation: 3s linear 1s infinite running sliding-vertically;
}
@keyframes sliding-vertically {
  0% {
    opacity: 0.33;
  }
  50% {
    opacity: 0.66;
  }
  100% {
    opacity: 1;
  }
}
